import { ElMessage } from 'element-plus';
 //Ëé∑ÂèñÊó∂Èó¥1ÔºàÂà∞Â§©Ôºâ
 function getNowDate_Day() {
    let date = new Date();
    let y = date.getFullYear();
    let m = date.getMonth() + 1;
    let d = date.getDate();
    m = m < 10 ? "0" + m : m;
    d = d < 10 ? "0" + d : d;
    let yarr=[y,m,d]
    return yarr.join("-") 
  }
   //Ëé∑ÂèñÊó∂Èó¥2ÔºàÂà∞ÁßíÔºâ
  function getNowDate() {
    let date = new Date();
    let y = date.getFullYear();
    let m = date.getMonth() + 1;
    let d = date.getDate();
    let H = date.getHours();
    let mm = date.getMinutes();
    let s=date.getSeconds()
    m = m < 10 ? "0" + m : m;
    d = d < 10 ? "0" + d : d;
    H = H < 10 ? "0" + H : H;
    let yarr=[y,m,d]
    let harr=[H,mm,s]
    return yarr.join("-") +" "+harr.join(":")
  }
  //Ê£ÄÊü•ÂÖÉÁ¥†ÊòØÂê¶‰∏∫Á©∫
  function isEmpty(d){
    d.replaceAll(' ','')
    if(d==''||d==null){
        ElMessage.error("can not be null !")
        return true
    }
    else{
        return false
    }
  }
export{isEmpty,getNowDate,getNowDate_Day}